<template>
    <div class="bd">
        <div class="ab-header clear">
            <div class="ab-box">
                <span class="drawer" @click="handleMenu()">☰</span>
                <div class="ab-content" :style="{'text-align': 'combination'}">
                    {{this.headTile}}
                </div>
                <span class="ab-right">  </span>
            </div>
        </div>
        <hr/>
        <drawer :show="open"/>
    </div>
</template>

<script>
    import Drawer from "@/components/drawer";

    export default {
        name: "common-head",
        props: {
            headTile: String
        },
        components: {
            Drawer
        },
        data() {
            return {
                open: false,
            }
        },
        watch: {},
        computed: {},
        methods: {
            handleMenu() {
                this.open = Math.random();
            },
        }
    }
</script>

<style scoped lang="less">
    .bd {
        padding: 15px 1px;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
    }
    .ab-header {
        /*margin-bottom: 15px;*/
        width: 100%;

        .ab-box {
            position: relative;
            height: 32px;
            line-height: 32px;

            .ab-content {
                width: 100%;
                font-size: 20px;
                font-weight: 500;
            }

            .dispersion {
                width: initial;
                display: inline;
            }

            .ab-headDate {
                vertical-align: middle;
                margin: 0 12px;
                font-size: 18px;
                color: #424953;
                -webkit-user-select: none;
                -webkit-touch-callout: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
            }

            .ab-right {
                position: absolute;
                top: 0;
                right: 10px;
                width: 46px;
                height: 26px;
                line-height: 26px;
                text-align: center;
                cursor: pointer;
            }

            .drawer {
                position: absolute;
                top: 0;
                left: 10px;
                cursor: pointer;
                width: 30px;
                height: 26px;
                line-height: 26px;
                text-align: center;
            }

            .dispersion-today {
                position: inherit;
            }

            .ab-center {
                margin: 0 auto;
            }

            .ab-left {
                float: left;
            }

            .ab-right {
                float: right;
            }
        }
    }
</style>