<template>
    <div>
        <common-head head-tile="猴子排序"></common-head>
        <div class="content">
            <div style="text-align: left; margin-top: 10px">
                <input id="txt" type="number" class="inpt" @change="changeNum" :disabled="inpDisabled" v-model="num"/>个随机数：
                <span>{{randomNum}}</span>
                <br/><br/>
                <button class="btn" v-show="!inpDisabled" @click="start" :disabled="inpDisabled">{{btnTxt}}</button>
                <button class="btn" v-show="inpDisabled" @click="stop">停止</button>
            </div>

            <div style=" margin-top: 20px; font-size: 18px;" id="rn" v-show="rnShow">
                第<span style="color: red">{{resultNum}}</span>次{{status}}
            </div>
            <div style="margin-top: 10px; font-size: 16px;" v-html="resultTxt">
            </div>
        </div>
    </div>
</template>

<script>
    import CommonHead from '@/components/common-head'

    export default {
        name: "nn",
        components: {
            CommonHead
        },
        data() {
            return {
                num: 5,
                numList: [],
                sortNum: 0,
                randomNum: '',
                resultNum: 0,
                resultTxt: '',
                btnTxt: '开始',
                inpDisabled: false,
                rnShow: false,
                intervalId: null,
                status: '',
            }
        },
        watch: {},
        computed: {},
        mounted() {
            this.changeNum();
        },
        methods: {
            changeNum() {
                this.sortNum = 0;
                let len = this.num;
                if (len < 2) {
                    this.numList = [];
                    this.randomNum = ''
                    alert('请输入大于1的整数')
                    return;
                }
                while (true) {
                    this.numList = [];
                    for (let i = 0; i < len; i++) {
                        let x = Math.floor(Math.random() * len)
                        this.numList.push(x)
                    }
                    if (!this.checkOrder(this.numList)) { // 无序的随机数
                        //console.log(numList)
                        break;
                    }
                }
                this.randomNum = this.numList;
                this.rnShow = false;
                this.resultNum = '';
                this.resultTxt = '';
                this.status = '';
            },

            start() {
                if (this.numList.length === 0) {
                    return;
                }
                this.btnTxt = '排序中...'
                this.inpDisabled = true
                this.sortNum = 0
                this.resultNum = '';
                this.resultTxt = '';
                this.status = '';
                this.intervalId = setInterval(() => {
                    this.sortNum++;
                    if (this.sortNum === 1) {
                        this.rnShow = true;
                    }
                    let n = this.shuffle(this.numList);
                    this.resultNum = this.sortNum;
                    this.resultTxt += this.sortNum + ": " + n + "<br/>"
                    if (this.checkOrder(n)) {
                        clearInterval(this.intervalId)
                        this.btnTxt = '开始'
                        this.inpDisabled = false
                        this.intervalId = null;
                        this.status = '(完成)';
                    }
                }, 10);
            },

            stop() {
                if (this.intervalId) {
                    clearInterval(this.intervalId)
                    this.btnTxt = '开始'
                    this.inpDisabled = false
                    this.intervalId = null;
                    this.status = '(中止)';
                }
            },

            // 洗牌函数
            shuffle(arr) {
                let res = [...arr]
                for (let i = arr.length - 1; i > 0; i--) {
                    let randomIndex = Math.floor(Math.random() * (i + 1))
                    let tmp = res[randomIndex]
                    res[randomIndex] = res[i]
                    res[i] = tmp
                }
                return res
            },

            // 校验是否
            checkOrder(arr) {
                for (let i = 1; i < arr.length; i++) {
                    if (arr[i] < arr[i - 1]) {
                        return false
                    }
                }
                return true;
            },
        }
    }
</script>

<style scoped lang="less">

    .content {
        text-align: left;
        padding: 0 20px;
    }

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
    }

    input[type="number"] {
        -moz-appearance: textfield;
    }

    .inpt {
        height: 36px;
        width: 100px;
        outline: none;
        border: 1px solid #1AB6FF;
        font-size: 24px;
        text-align: center;
    }

    .btn {
        background-color: #1AB6FF;
        color: white;
        border: 0;
        height: 40px;
        width: 120px;
        font-size: 18px;
    }

    .btn:hover {
        background-color: #1B85EE;
    }

</style>