<template>
    <div class="atlist">
        <div :class="open ? 'mask' : ''" @click="close"></div>
        <div :class="open ? 'open' : 'close'">
            <div class="header">
                <div class="cool-text">目录</div>
            </div>
            <div class="item" v-for="(book, index) in books" :key="index" @click="selectBook(book)">
                {{book}}
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            show: [String, Number, Boolean]
        },
        watch: {
            show: function (v) {
                this.open = !!v;
            }
        },
        data() {
            return {
                open: false,
                books: [],
            }
        },
        mounted() {
            this.loadBooks('static/book/books.txt')
        },
        methods: {
            close() {
                this.open = false
            },
            loadBooks(path) {
                fetch(path)
                    .then(response => response.text())
                    .then(text => {
                        text = this.replaceAll(text, '\r\n', '@#@');
                        text = this.replaceAll(text, '\n', '@#@');
                        this.books = text.split("@#@")
                        if (this.books.length > 0) {
                            this.$emit('selectBook', this.books[0])
                        }
                    })
                    .catch(error => {
                        console.error('读取文件失败:', error);
                    });
            },
            replaceAll(str, find, replace) {
                return str.replace(new RegExp(find, 'g'), replace);
            },
            selectBook(book) {
                this.open = false;
                this.$emit('selectBook', book)
            }
        }
    }
</script>

<style scoped lang="less">
    .atlist {

        .mask {
            position: fixed;
            z-index: 10;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            opacity: 1;
            background: rgba(0, 0, 0, 0.7);
        }

        .close {
            position: absolute;
            z-index: 10;
            left: -300px;
            top: 0;
            width: 200px;
            height: 100%;
            background: #fff;
            /*transition: left linear 0.5s;*/
        }

        .open {
            position: absolute;
            z-index: 10;
            right: 0;
            top: 0;
            width: 300px;
            height: 100%;
            background: #fff;
            transition: right linear 0.2s;

            .header {
                margin: 0;
                height: 55px;
                line-height: 55px;
                border-bottom: 1px solid silver;
                background-color: #59bbff;
                color: white;

                .cool-text {
                    text-align: center;
                    font-size: 1.2rem;
                    font-weight: 500;
                    color: white;
                }

            }

            .item {
                height: 50px;
                line-height: 50px;
                border-bottom: 1px silver solid;
                text-align: left;
                cursor: pointer;
                margin: 0;
                padding: 0 10px;

                .link {
                    width: 100%;
                    height: 100%;
                    display: block;
                }

                a {
                    text-decoration: none;
                    color: #000000;
                }
            }
        }
    }

</style>
