<template>
    <div>
        <common-head head-tile="记忆翻牌"></common-head>
        <div class="content">
            <div style="display: flex">
                <div style="flex: 1;">
                    <div style="padding-left: 10px; ">
                        <span>难度：</span>
                        <select @change="selectLevel" style="width: 60px" v-model="level">
                            <option value="0">1级</option>
                            <option value="1">2级</option>
                            <option value="2">3级</option>
                            <option value="3">4级</option>
                            <option value="4">5级</option>
                        </select>
                    </div>
                </div>

                <div style="flex: 2;">
                    <div style="text-align: right; padding-right: 10px; ">
                        <span>点击次数：</span>
                        <span v-html="flipNum"></span>
                        <span style="margin-left: 20px">耗时：</span>
                        <span v-html="timeUsed"></span>
                    </div>
                </div>

            </div>

            <div>

            </div>

            <div class="flip-area">
                <ul class="table" ref="table" id="table">
                    <li v-for="(item, ri) in rank.row" :key="ri">
                        <div v-for="(item, ci) in rank.col" :id='"img" + ri + "-" + ci' @click='flip' :key='ci' class="s-img"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import CommonHead from '@/components/common-head'

    const ranks = [
        {col: 2, row: 2},
        {col: 4, row: 4},
        {col: 6, row: 6},
        {col: 6, row: 8},
        {col: 6, row: 10},
    ]
    const themes = [
        {name: "window", types: 36, ext: ".bmp", format: "/win/win (@).bmp"},
        {name: "emoji", types: 30, ext: ".png", format: "/emoji/emoji (@).png"},
    ]

    export default {
        name: "nn",
        components: {
            CommonHead
        },
        data() {
            return {
                rendered: false,
                lastFlipIndex: null,
                flipNum: 0,
                timer: null,
                timeUsed: '00:00',
                level: 1,
                rank: ranks[1],
            }
        },
        watch: {},
        computed: {},
        mounted() {
            this.render(1)
        },
        methods: {
            selectLevel() {
                this.render(this.level)
            },

            init() {
                this.lastFlipIndex = null;
                this.flipNum = 0
                if (this.timer) {
                    clearInterval(this.timer)
                }
                this.timer = null;
                this.timeUsed = "00:00"
            },

            /**
             * 元素组合
             * @param types   元素种类
             * @param level   等级
             * @returns {*[]}
             */
            func(types, level) {
                const rank = ranks[level]
                const size = rank.col * rank.row;
                let list = [];
                for (let i = 0, len = size / 2; i < len; i++) {
                    let x = i % types
                    // 数必须出现至少两次
                    list.push(x)
                    list.push(x)
                }
                return this.shuffle(list);
            },


            /**
             * 洗牌函数
             * @param arr 数组
             * @returns {*[]}
             */
            shuffle(arr) {
                let res = [...arr]
                for (let i = arr.length - 1; i > 0; i--) {
                    let randomIndex = Math.floor(Math.random() * (i + 1))
                    let tmp = res[randomIndex]
                    res[randomIndex] = res[i]
                    res[i] = tmp
                }
                return res
            },

            //console.log(func(5, 10))

            render(level) {
                this.init();
                this.rank = ranks[level]

                this.$nextTick(() => {
                    // DOM 更新后的操作
                    let row = this.rank.row;
                    let col = this.rank.col;
                    const size = col * row;
                    let w = this.$refs.table.clientWidth / col;
                    if (w > 62) {
                        w = 62
                    }
                    let theme = themes[0];
                    const num = this.func(theme.types, level);
                    let index = 0;
                    for (let i = 0; i < row; i++) {
                        for (let j = 0; j < col; j++) {
                            let group = num[index] + 1;
                            let imgName = theme.format.replace("@", group);
                            let imgSrc = "static/img" + imgName;
                            let imgObj = document.getElementById("img" + i + "-" + j);
                            imgObj.style.width = w + "px";
                            imgObj.style.height = w + "px";
                            imgObj.style.background = "skyblue"
                            imgObj.setAttribute("img-src", imgSrc);
                            imgObj.setAttribute("group", group);
                            imgObj.setAttribute("index", i + "-" + j);
                            imgObj.setAttribute("size", size);
                            imgObj.setAttribute("show", "false");
                            this.preload(imgSrc)
                            index++;
                        }
                    }
                });
            },

            // 预加载图片
            preload(src) {
                const img1 = new Image();//创建一个img对象
                img1.src = src//图片地址
            },

            flip(event) {
                let thisObj = event.target
                if (thisObj.getAttribute("show") !== "true" && this.lastFlipIndex !== thisObj.getAttribute("index")) {
                    this.flipNum++;
                }
                // 判断和上次的翻面是否相同
                if (this.lastFlipIndex && thisObj.getAttribute("group") === document.getElementById("img" + this.lastFlipIndex).getAttribute("group") && this.lastFlipIndex !== thisObj.getAttribute("index")) {
                    thisObj.setAttribute("show", "true");
                    let lastObj = document.getElementById("img" + this.lastFlipIndex);
                    // 把上一次的显示出来
                    lastObj.setAttribute("show", "true");
                    lastObj.style.background = "url('" + lastObj.getAttribute("img-src") + "') no-repeat";
                    lastObj.style.backgroundSize = "100% 100%";
                    this.lastFlipIndex = null;
                } else {
                    this.lastFlipIndex = thisObj.getAttribute("index");
                }
                let finish = true;
                // 翻面
                const objs = document.getElementsByClassName("s-img");
                for (let i = 0; i < objs.length; i++) {
                    const obj = objs[i];
                    if (obj.getAttribute("show") !== 'true') {
                        obj.style.background = "skyblue"
                        finish = false;
                    }
                }
                // 显示出来
                thisObj.style.background = "url('" + thisObj.getAttribute("img-src") + "') no-repeat";
                thisObj.style.backgroundSize = "100% 100%";
                // 翻面次数
                this.showResult(finish);
            },

            showResult(finish) {
                // 计时
                if (this.flipNum === 1) {
                    let count = 0;
                    const _this = this;
                    this.timer = setInterval(function () {
                        count++;
                        let fen = Math.floor(count / 60)
                        let miao = count % 60
                        if (fen < 10) {
                            fen = "0" + fen;
                        }
                        if (miao < 10) {
                            miao = "0" + miao;
                        }
                        _this.timeUsed = fen + ":" + miao
                    }, 1000);
                }
                if (finish) {
                    if (this.timer) {
                        clearInterval(this.timer);
                    }
                }
            },
        }
    }
</script>

<style  lang="less">

    .content {
        text-align: left;
        margin: 0;
    }

    .flip-area {
        margin-top: 20px;
        display: flex;

        ul {
            padding: 0;
            margin: 0;
        }

        .table {
            display: table;
            border-spacing: 1px;
            margin: auto;

            li {
                display: table-row;
            }

            div {
                display: table-cell;
                text-align: center;
                height: 65px;
                width: 65px;
                background-color: skyblue;
            }
        }


    }




</style>