<template>
    <div>
        <common-head head-tile="生辰八字"></common-head>
        <div class="content">
            <div>
                <span class="srq">性别：</span>
                <label>
                    <input type="radio" value="1" v-model="sexSel" name="sex" @change="handleLunisolar">
                    <span>男</span>
                </label>
                <label :style="{marginLeft: '20px'}">
                    <input type="radio" value="0" v-model="sexSel" name="sex" @change="handleLunisolar">
                    <span>女</span>
                </label>
            </div>
            <div :style="{marginTop: '10px'}">
                <div class="">
                    <span class="srq">日期：</span>
                    <input readonly="readonly" type="text" class="inpt" @click="showCalendar = true" v-model="dateTxt"/>
                </div>
            </div>
            <div :style="{marginTop: '10px'}">
                <span class="srq">时辰：</span>
                <label  @change="handleLunisolar">
                    <select v-model="hSel" class="selt">
                        <option v-for="(item, index) in hourOpts" :value="item.id" :key="index">{{item.name}}</option>
                    </select>
                </label>
            </div>
            <div :style="{marginTop: '10px'}">
                公历： {{ob?.format('YYYY年MM月DD日 lH时')}}
            </div>
            <div :style="{marginTop: '5px'}">
                农历： {{ob?.format('lY年lMlD lH时')}}
            </div>
            <hr/>
            <div :style="{fontSize: '20px'}">
                <b>{{c8ex?.sex}}:&nbsp;&nbsp;</b><b>{{c8ex?.year}}</b>
                <b :style="{marginLeft: '10px'}">{{c8ex?.month}}</b>
                <b :style="{marginLeft: '10px'}">{{c8ex?.day}}</b>
                <b :style="{marginLeft: '10px'}">{{c8ex?.hour}}</b>
                <span v-if="c8ex?.missing" :style="{marginLeft: '10px'}">
                    (<b v-for="item in c8ex?.missing">{{item.name}}</b><b>空</b>)
                </span>
            </div>
            <div :style="{marginTop: '5px'}">
                <b>胎元:&nbsp;&nbsp;</b><span>{{c8ex?.embryo().name}} ({{c8ex?.embryo().takeSound}})</span>
            </div>
            <div>
                <b>命宫:&nbsp;&nbsp;</b><span>{{c8ex?.ownSign().name}} ({{c8ex?.ownSign().takeSound}})</span>
            </div>
            <div>
                <b>身宫:&nbsp;&nbsp;</b><span>{{c8ex?.bodySign().name}} ({{c8ex?.bodySign().takeSound}})</span>
            </div>
            <div :style="{marginTop: '10px'}">
                <table class="table">
                    <tr>
                        <th>五行:</th>
                        <td>{{c8ex?.year.stem.e5.name + c8ex?.year.branch.e5.name}}</td>
                        <td>{{c8ex?.month.stem.e5.name + c8ex?.month.branch.e5.name}}</td>
                        <td>{{c8ex?.day.stem.e5.name + c8ex?.day.branch.e5.name}}</td>
                        <td>{{c8ex?.hour.stem.e5.name + c8ex?.hour.branch.e5.name}}</td>
                    </tr>
                    <tr>
                        <th>纳音:</th>
                        <td>{{c8ex?.year.takeSound}}</td>
                        <td>{{c8ex?.month.takeSound}}</td>
                        <td>{{c8ex?.day.takeSound}}</td>
                        <td>{{c8ex?.hour.takeSound}}</td>
                    </tr>
                    <tr>
                        <th>十神:</th>
                        <td>{{c8ex?.year.stemTenGod.name}}</td>
                        <td>{{c8ex?.month.stemTenGod.name}}</td>
                        <td>{{c8ex?.day.stemTenGod.name}}</td>
                        <td>{{c8ex?.hour.stemTenGod.name}}</td>
                    </tr>
                    <tr>
                        <th rowspan ="2" >藏干:</th>
                        <td>
                            <span v-for="item in c8ex?.year.branch.hiddenStems">{{item.name}}</span>
                        </td>
                        <td>
                            <span v-for="item in c8ex?.month.branch.hiddenStems">{{item.name}}</span>
                        </td>
                        <td>
                            <span v-for="item in c8ex?.day.branch.hiddenStems">{{item.name}}</span>
                        </td>
                        <td>
                            <span v-for="item in c8ex?.hour.branch.hiddenStems">{{item.name}}</span>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <div v-for="item in c8ex?.year.branchTenGod">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.month.branchTenGod">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.day.branchTenGod">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.hour.branchTenGod">{{item.name}}</div>
                        </td>
                    </tr>
                    <tr>
                        <th>神煞:</th>
                        <td>
                            <div v-for="item in c8ex?.year.gods">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.month.gods">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.day.gods">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.hour.gods">{{item.name}}</div>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
        <date-picker :showCalendar="showCalendar" @confirm="selectDate"  @cancel="showCalendar = false"> </date-picker >
    </div>
</template>

<script>
    import CommonHead from '@/components/common-head'
    import DatePicker from '@/components/date-picker/index'

    import lunisolar from 'lunisolar'
    import zhCn from 'lunisolar/locale/zh-cn'
    import {theGods} from '@lunisolar/plugin-thegods'
    import theGodsZhCn from '@lunisolar/plugin-thegods/locale/zh-cn'
    import { char8ex } from '@lunisolar/plugin-char8ex'
    import char8exZhCn from '@lunisolar/plugin-char8ex/locale/zh-cn'

    lunisolar.extend(theGods).extend(char8ex)
    lunisolar.locale(theGodsZhCn).locale(char8exZhCn).locale(zhCn)

    export default {
        name: "bazi",
        components: {
            CommonHead,
            DatePicker
        },
        data() {
            let now = new Date();
            return {
                showCalendar: false,
                dateTxt: `公历：${now.getFullYear()}年${now.getMonth() + 1}月${now.getDate()}日`,
                sexSel: '1',
                rliSel: 'gli',
                ySel: now.getFullYear(),
                mSel: now.getMonth() + 1,
                dSel: now.getDate(),
                hSel: now.getHours(),
                ob: null,
                c8ex: null,
            }
        },
        watch: {
        },
        computed: {
            hourOpts() {
                return [
                    {id:0,  name: '子时 (23:00~00:59)'},
                    {id:2,  name: '丑时 (01:00~02:59)'},
                    {id:4,  name: '寅时 (03:00~04:59)'},
                    {id:6,  name: '卯时 (05:00~06:59)'},
                    {id:8,  name: '辰时 (07:00~08:59)'},
                    {id:10, name: '巳时 (09:00~10:59)'},
                    {id:12, name: '午时 (11:00~12:59)'},
                    {id:14, name: '未时 (13:00~14:59)'},
                    {id:16, name: '申时 (15:00~16:59)'},
                    {id:18, name: '酉时 (17:00~18:59)'},
                    {id:20, name: '戌时 (19:00~20:59)'},
                    {id:22, name: '亥时 (21:00~22:59)'},
                ];
            },
        },
        mounted() {
            if (this.hSel % 2 !== 0) {
                this.hSel += 1;
                if (this.hSel === 24) {
                    this.hSel = 0;
                }
            }
            this.handleLunisolar();
        },
        methods: {
            selectDate(d) {
                this.showCalendar = false
                this.dateTxt = (d.type === 'solar' ? '公历：': '农历：') + d.year.text + d.month.text + d.day.text;
                this.rliSel = d.type === 'solar' ? 'gli' : 'nli'
                this.ySel = d.year.value;
                this.mSel = d.month.value;
                this.dSel = d.day.value;
                this.handleLunisolar();
            },
            handleLunisolar() {
                if (this.rliSel === 'nli') {// 农历
                    let ymd = {
                        year: this.ySel,
                        month: this.mSel,
                        day: this.dSel,
                        hour: this.hSel / 2, // 从0开始，0 ~ 11 对应 子时 ~ 亥时
                    }
                    try {
                        // console.log(ymd)
                        this.ob = lunisolar.fromLunar(ymd)
                    } catch (e) {
                        this.ob = null;
                        this.c8ex = null;
                    }
                } else {
                    let ymd = this.ySel + "-" + (this.mSel < 10 ? "0" + this.mSel : this.mSel) + "-" + this.dSel + " " + this.hSel + ":00";
                    this.ob = lunisolar(ymd)
                }
                if (this.ob != null) {
                    this.c8ex = this.ob.char8ex(this.sexSel);
                }
            }
        }
    }
</script>

