    .content {
        text-align: left;
        padding: 0 10px;
        .table {
            width: 100%;
            border-collapse: collapse;
            th {
                border: 1px silver dashed;
            }
            td {
                border: 1px silver dashed;
                text-align: center;
            }
        }

        .inpt {
            height: 30px;
            width: 260px;
            outline: none;
            border: 1px solid darkgrey;
            font-size: 17px;
            color: #666666;
