<template>
    <div class="ath">
        <div class="at-header clear">
            <div class="at-box">
                <span class="drawer" @click="handleMenu()">☰</span>
                <div class="at-content" :style="{'text-align': 'combination'}">
                    {{this.headTile}}
                </div>
                <span class="at-right" @click="listShow = Math.random()">※</span>
            </div>
        </div>
        <hr/>
        <drawer :show="open"/>
        <article-list :show="listShow" @selectBook="selectBook"/>
    </div>
</template>

<script>
    import Drawer from "@/components/drawer";
    import ArticleList from "@/components/article-list";

    export default {
        name: "article-head",
        components: {
            Drawer,
            ArticleList
        },
        data() {
            return {
                open: false,
                listShow: false,
                headTile: ""
            }
        },
        watch: {},
        computed: {},
        methods: {
            handleMenu() {
                this.open = Math.random();
            },
            selectBook(book) {
                this.$emit('selectBook', book);
                this.headTile = book
            }
        }
    }
</script>

<style scoped lang="less">
    .ath {
        padding: 15px 1px;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
    }
    .at-header {
        /*margin-bottom: 15px;*/
        width: 100%;

        .at-box {
            position: relative;
            height: 32px;
            line-height: 32px;

            .at-content {
                width: 100%;
                font-size: 20px;
                font-weight: 500;
            }

            .dispersion {
                width: initial;
                display: inline;
            }

            .at-headDate {
                vertical-align: middle;
                margin: 0 12px;
                font-size: 18px;
                color: #424953;
                -webkit-user-select: none;
                -webkit-touch-callout: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
            }

            .at-right {
                position: absolute;
                top: 0;
                right: 10px;
                width: 46px;
                height: 26px;
                line-height: 26px;
                text-align: center;
                cursor: pointer;
            }

            .drawer {
                position: absolute;
                top: 0;
                left: 10px;
                cursor: pointer;
                width: 30px;
                height: 26px;
                line-height: 26px;
                text-align: center;
            }

            .dispersion-today {
                position: inherit;
            }

            .at-center {
                margin: 0 auto;
            }

            .at-left {
                float: left;
            }

            .at-right {
                float: right;
            }
        }
    }
</style>