<template>
    <div class="dp-mask" v-if="showCalendar">
        <div class="calendar">
            <div class="content" @tap.stop>
                <div class="title choose-title" :style="titleStyle" v-show="true">
                    {{ (isSolar ?'公历:': '农历:')+chooseYear.text+chooseMonth.text+chooseDay.text }}
                </div>
                <div class="title">
                    <div class="switch" :style="{border:'1px solid '+ tabBorderColor}">
                        <div class="left"
                             :style="type === 'solar'?tabActive:'color:'+tabActive.background"
                             @click="switchBtn('solar')">公历
                        </div>
                        <div class="right"
                             :style="type === 'lunar'?tabActive:'color:'+tabActive.background"
                             @click="switchBtn('lunar')">农历
                        </div>
                        <div class="bg" :class="type"></div>
                    </div>
                    <!-- <div class="ignore-year">忽略年份</div> -->
                </div>
                <div class="time-content" style="padding: 0 10px;width:96%;">
                    <div class="time-list">
                        <div class="time-item">
                            <scroll-select @change="yearsChange"
                                          :isChange="isSolar"
                                          :chooseStyle="chooseStyle"
                                          :value="sureYear"
                                          :options="years"></scroll-select>
                        </div>
                        <div class="time-item">
                            <scroll-select
                                    @change="monthsChange"
                                    :isChange="isSolar"
                                    :chooseStyle="chooseStyle"
                                    :value="sureMonth"
                                    :options="months"></scroll-select>
                        </div>
                        <div class="time-item">
                            <scroll-select
                                    @change="daysChange"
                                    :isChange="isSolar"
                                    :chooseStyle="chooseStyle"
                                    :value="sureDay"
                                    :options="days"></scroll-select>
                        </div>
                    </div>
                </div>
                <div class="bottom">
                    <div class="left" :style="cancelStyle" @click="cancel">取消</div>
                    <div class="right" :style="confirmStyle" @click="confirm">确定</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import ScrollSelect from './scroll-select.vue'
    import lunisolar from 'lunisolar'
    import zhCn from 'lunisolar/locale/zh-cn'

    lunisolar.locale(zhCn)

    const solarMonthDays = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
    const nStr1 = ['日','一','二','三','四','五','六','七','八','九','十']
    const nStr2 = ['初','十','廿','卅']
    const nStr3 = ['正','二','三','四','五','六','七','八','九','十','冬','腊']
    const nStr4 = ['零','一','二','三','四','五','六','七','八','九','十']


    export default {
        name: 'DatePicker',
        components: {ScrollSelect},
        data() {
            let now = new Date();
            return {
                type: 'solar', // lunar农历，solar公历
                isSolar: true, // true公历
                checked: true,
                years: [],
                months: [],
                days: [],
                leap_month: 0, // 1为闰月0为非闰月
                sureYear: now.getFullYear(), // 默认弹出显示年份
                sureMonth: now.getMonth() + 1, // 默认弹出显示月份
                sureDay: now.getDate(), // 默认弹出显示日
                chooseYear: {
                    value: now.getFullYear(),
                    text: now.getFullYear() + '年'
                },
                chooseMonth: {
                    value: now.getMonth() + 1,
                    text: (now.getMonth() + 1) + '月'
                },
                chooseDay: {
                    value: now.getDate(),
                    text: now.getDate() + '日'
                },
            }
        },

        props: {
            showCalendar: {
                type: Boolean,
                default: () => false,
            },
            // 标题样式
            titleStyle: {
                type: Object,
                default: () => ({})
            },
            // tab边框
            tabBorderColor: {
                type: String,
                default: '#1989fa'
            },
            // 活跃时tab样式
            tabActive: {
                type: Object,
                default: () => ({
                    color: '#fff',
                    background: '#1989fa'
                })
            },
            // 取消按钮样式
            cancelStyle: {
                type: Object,
                default: () => ({
                    color: '#a6a6a6'
                })
            },
            // 完成按钮样式
            confirmStyle: {
                type: Object,
                default: () => ({
                    color: '#fff',
                    background: '#1989fa'
                })
            },
            // 选中项的样式
            chooseStyle: {
                type: Object,
                default: () => ({
                    color: '#1989fa'
                })
            }
        },
        watch: {},
        created() {
            this.getTime()
        },
        mounted() {

        },
        methods: {
            // 选择年份
            yearsChange(e) {
                this.chooseYear = e
                this.sureYear = e.value
                this.bindChange(e)
            },
            // 选择月份
            monthsChange(e) {
                this.chooseMonth = e
                this.sureMonth = e.value
                this.bindChange(e)
            },
            // 选择天
            daysChange(e) {
                this.chooseDay = e
                this.sureDay = e.value
            },
            cancel() {
                this.$emit('cancel')
            },
            // 提交
            confirm() {
                let data = {
                    year: this.chooseYear,
                    month: this.chooseMonth,
                    day: this.chooseDay,
                    type: this.type
                }
                this.$emit('confirm', data)
            },
            // 进来获取公历的1990-02-10 10,初始化
            getTime() {
                this.years = []
                for (let i = 1910; i <= 2099; i++) {
                    if (this.isSolar) {
                        this.years.push({
                            value: i,
                            text: i + '年'
                        })
                    } else {
                        this.years.push({
                            value: i,
                            text: this.toChinaYear(i) + '年'
                        })
                    }
                }
                if (this.isSolar) {
                    this.getSolarMonthDays(this.sureYear, this.sureMonth)
                } else {
                    this.getLunarMonthDays(this.sureYear, this.sureMonth)
                }
            },
            bindChange: function (e) {
                // 公历返回每个月天数
                if (this.isSolar) {
                    this.getSolarMonthDays(this.sureYear, this.sureMonth)
                } else {
                    this.getLunarMonthDays(this.sureYear, this.sureMonth)
                }
            },
            // 获取公历的月份和天数
            getSolarMonthDays(year, month) {
                this.months = []
                for (let i = 1; i <= 12; i++) {
                    this.months.push({
                        value: i,
                        text: i + '月'
                    })
                }
                let mdays = solarMonthDays[month - 1];
                if (month === 2) {
                    // 闰年
                    mdays = (year % 4 === 0 && year % 100 !== 0) || year % 400 === 0 ? 29 : 28
                }
                this.days = []
                for (let i = 1; i <= mdays; i++) {
                    this.days.push({
                        value: i,
                        text: i + '日'
                    })
                }
            },
            // 获取农历的月份和天数
            getLunarMonthDays(year, month) {
                const ob = lunisolar.fromLunar({year, month, day: 1})
                const leap_month = ob.lunar.leapMonth
                // 返回农历 闰月没有就返回0
                this.months = [];
                for (let i = 1; i <= 12; i++) {
                    this.months.push({
                        value: i,
                        text: this.toChinaMonth(i) + "月"
                    })
                    if (i === leap_month) {
                        this.months.push({
                            value: 100 + i,
                            text: '闰' + this.toChinaMonth(i) + "月"
                        })
                    }
                }
                const mdays = ob.lunar.isBigMonth ? 30 : 29;
                this.days = [];
                for (let i = 1; i <= mdays; i++) {
                    this.days.push({
                            value: i,
                            text: this.toChinaDay(i)
                        }
                    )
                }
            },
            switchBtn(type) {
                // 农历公历互相联动切换
                if (this.type === type) {
                    return false
                }
                //console.log(type, this.sureYear + "-" + this.sureMonth + "-" + this.sureDay)
                if (type === 'lunar') {
                    const ob = lunisolar(this.sureYear + "-" + this.sureMonth + "-" + this.sureDay)
                    // 公历切换为农历
                    this.sureYear = ob.lunar.year // 默认弹出显示年份
                    this.sureMonth = ob.lunar.month  // 默认弹出显示月份
                    this.sureDay = ob.lunar.day
                    //console.log(this.sureYear + "-" + this.sureMonth + "-" + this.sureDay)
                } else {
                    const ob = lunisolar.fromLunar({
                        year: this.sureYear,
                        month: this.sureMonth,
                        day: this.sureDay,
                    })
                    // 农历切换为公历
                    this.sureYear = ob.year // 默认弹出显示年份
                    this.sureMonth = ob.month  // 默认弹出显示月份
                    this.sureDay = ob.day
                }
                this.isSolar = type === 'solar'
                this.type = type
                this.getTime()
            },
            /**
             * 传入年份返回汉语通俗表示法
             * @param {lunar} year
             * @return CN {string}
             * @eg:let cnYear = calendar.toChinaYear(1960);//cnYear = '一九六零'
             */
            toChinaYear(y) {
                //年
                let year = y.toString().split('')
                return `${nStr4[year[0]]}${nStr4[year[1]]}${nStr4[year[2]]}${nStr4[year[3]]}`
            },
            /**
             * 传入农历数字月份返回汉语通俗表示法
             * @param lunar month
             * @return Cn string
             * @eg:var cnMonth = calendar.toChinaMonth(12) ;//cnMonth='腊月'
             */
            toChinaMonth(m) {
                // 月
                if (m > 12 || m < 1) {
                    return -1
                } //若参数错误 返回-1
                var s = nStr3[m - 1]
                return s
            },

            /**
             * 传入农历日期数字返回汉字表示法
             * @param lunar day
             * @return Cn string
             * @eg:var cnDay = calendar.toChinaDay(21) ;//cnMonth='廿一'
             */
            toChinaDay(d) {
                //日 => \u65e5
                var s
                switch (d) {
                    case 10:
                        s = '初十'
                        break
                    case 20:
                        s = '二十'
                        break
                    case 30:
                        s = '三十'
                        break
                    default:
                        s = nStr2[Math.floor(d / 10)]
                        s += nStr1[d % 10]
                }
                return s
            },
        },
    }
</script>

