<template>
    <div>
        <article-head @selectBook="selectBook"></article-head>
        <div class="content" :style="{'text-align': isMobile ? 'left' : 'center'}">
            <span v-for="(word, index) in words" :key="index"
                  :class="{ 'word-enter-active': true, 'word-leave-active': false }"
                  :style="{ 'animation-delay': index * delay + 'ms' }">
                <span v-if="word === '\n'">
                    <br/>
                </span>
                <ruby v-else-if="isChinese(word)" class="word">
                    {{ word }}
                    <rp>(</rp>
                    <rt>{{pinyin(word)}}</rt>
                    <rp>)</rp>
                </ruby>
                <span v-else>
                    {{word}}
                </span>
            </span>
        </div>
    </div>
</template>

<script>
    import ArticleHead from '@/components/article-head'
    import {pinyin, addDict, customPinyin} from 'pinyin-pro';
    import CompleteDict from '@pinyin-pro/data/complete.json';

    addDict(CompleteDict);

    export default {
        name: "Article",
        components: {
            ArticleHead
        },
        data() {
            return {
                words: null,
                wordNum: 0,
                delay: 30,
                isMobile: true
            }
        },
        watch: {},
        computed: {},
        mounted() {
            this.checkMobile();
            //this.loadTextFile('/static/book/静夜思.txt')
        },
        methods: {
            selectBook(book) {
                this.loadTextFile(`static/book/${book}.txt`)
            },
            checkMobile() {
                const userAgent = navigator.userAgent || navigator.vendor || window.opera;
                console.log(userAgent)
                this.isMobile = /android|webos|iphone|ipad|ipod|blackberry|iemobile|opera mini/i.test(userAgent);
            },
            loadTextFile(path) {
                this.words = null
                fetch(path)
                    .then(response => response.text())
                    .then(text => {
                        text = this.replaceAll(text, '\r\n', '\n');
                        this.words = text.split("")
                        this.wordNum = this.words.length
                    })
                    .catch(error => {
                        console.error('读取文件失败:', error);
                    });
            },
            isChinese(text) {
                return /^[\u4e00-\u9fa5]+$/.test(text.trim());
            },
            pinyin(text) {
                return pinyin(text)
            },
            replaceAll(str, find, replace) {
                return str.replace(new RegExp(find, 'g'), replace);
            }
        }
    }
</script>

<style scoped lang="less">

    .content {
        white-space: nowrap;
        overflow-x: auto;
        padding: 0 10px;
        font-size: 20px;

        ruby {
        }

        rt {
            font-size: 14px;
            width: 42px;
            color: grey;
        }

        .word {
            word-spacing: 4px
        }
    }

    .word-enter-active, .word-leave-active {
        animation: enter 0.5s ease-out forwards;
    }

    @keyframes enter {
        0% {
            opacity: 0;
            transform: translateY(-20px);
        }
        100% {
            opacity: 1;
            transform: translateY(0);
        }
    }

    /* 隐藏水平滚动条 */
    ::-webkit-scrollbar {
        display: none;
    }

</style>