import Vue from 'vue'
import VueRouter from 'vue-router'
import Vdate from '@/views/vdate'
import Bazi from '@/views/bazi'
import About from '@/views/about'
import Nn from '@/views/nn'
import Flip from '@/views/flip'
import Bogo from '@/views/bogo'
import Article from '@/views/article'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'vdate',
    component: Vdate
  },
  {
    path: '/bazi',
    name: 'bazi',
    component: Bazi
  },
  {
    path: '/article',
    name: 'article',
    component: Article
  },
  {
    path: '/nn',
    name: 'nn',
    component: Nn
  },
  {
    path: '/flip',
    name: 'flip',
    component: Flip
  },
  {
    path: '/bogo',
    name: 'bogo',
    component: Bogo
  },
  {
    path: '/about',
    name: 'about',
    component: About
  },
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
